import { BsToggleOff, BsToggleOn } from "react-icons/bs";
import { connect } from "react-redux";
import { switchDetailedView } from "../../../../redux/settings/settings.actions";
import { SettingsItem } from "../settings.component";

let SwitchMode = ({configs, switchDetailedView})=>{
    const {detailedView} = configs
    let switchModeTitle = detailedView?"Detailed View":"Switch to Detailed View";
    return(
        <SettingsItem title={switchModeTitle}
            className="switch-mode"
            status = {detailedView}
            action = {switchDetailedView}
            enabled={
                <BsToggleOn color="green"/>
            }
            disabled={
                <BsToggleOff/>
            }
        />
    )
}

let mapStateToProps = (state)=>({
    configs: state.configs
})
  
let mapDispatchToProps = (dispatch)=>({
    switchDetailedView: (detailedView)=>(dispatch(switchDetailedView(detailedView)))
})

export default connect(mapStateToProps, mapDispatchToProps)(SwitchMode);