import { BsPerson, BsPersonFill } from "react-icons/bs";
import { AiOutlineLogout } from "react-icons/ai";
import { connect } from "react-redux";
import { auth, SignInWithGoogle } from "../../../../utils/firebase/firebase.utils";
import { SettingsItem } from "../settings.component";

let Profile = ({user})=>{
    let profileTitle = user?user.displayName:"Sign In";
    let signOutText = "Sign Out"
    let action = user?null:SignInWithGoogle
    console.log(user);
    return(
        <div>
            <SettingsItem title={profileTitle}
                status = {user}
                action = {action}
                enabled={
                    <BsPersonFill/>
                }
                disabled={
                    <BsPerson/>
                }
            />
            {
                user
                ?(
                    <SettingsItem title={signOutText}
                        status={true}
                        action = {()=>auth.signOut()}
                        enabled={
                            <AiOutlineLogout/>
                        }
                    />
                )
                :null
            }
        </div>
    )
}

let mapStateToProps = (state)=>({
    user: state.user
})

export default  connect(mapStateToProps)(Profile);