import { connect } from 'react-redux'
import { BsGear } from 'react-icons/bs'
import { toggleSettingsMenu } from '../../../redux/settings/settings.actions';
import SwitchMode from './switch-mode/switch-mode.component';
import Profile from './profile/profile.component';
import './settings.styles.scss'

let Settings = ({configs, toggleSettingsMenu})=>{
    const {toggleSettings} = configs
    return(
        <div className="settings">
            <BsGear onClick={()=>toggleSettingsMenu(toggleSettings)}/>
            {
                toggleSettings
                ?(
                    <div className="settings-submenu">
                        <SwitchMode/>
                        <Profile/>
                    </div>
                )
                :null
            }
        </div>
    )
}


export const SettingsItem = ({title, status, action, enabled, disabled, className})=>{
    return(
        <div className={`setting-item ${className}`} onClick={action?()=>action(status):null}>
            {
                status
                ? enabled
                : disabled
            }
            {
                title
                ?<label className="label">{title}</label>
                :null

            }
        </div>
    )
}

let mapStateToProps = (state)=>({
    configs: state.configs,
    user: state.user
})
  
let mapDispatchToProps = (dispatch)=>({
    toggleSettingsMenu: (toogleSettings)=>(dispatch(toggleSettingsMenu(toogleSettings)))
})

export default  connect(mapStateToProps, mapDispatchToProps)(Settings);